.form-block{
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}
.form-block label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
.form-block input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
}
.form-block button{
    text-align: right;
}